@font-face{font-family:"양진체";src:url("https://fastly.jsdelivr.net/gh/supernovice-lab/font@0.9/yangjin.woff") format("woff");font-weight:normal;font-style:normal}.VisionContainer{font-family:"양진체";display:flex;align-items:center;justify-content:space-between;padding:2em;width:80%;height:100%}.VisionContainer .VisionList{font-size:3em;margin-right:2em;width:80%}.VisionContainer .VisionList.fade-in{opacity:0;transform:translateY(20px);animation:fadeIn .5s forwards}.VisionContainer .VisionList.fade-out{opacity:1;transform:translateY(0);animation:fadeOut .5s forwards}.VisionContainer .VisionNav{display:flex;flex-direction:column;gap:2em}.VisionContainer .VisionNav .nav-button{padding:.5em 1em;font-size:1em;color:#000;background-color:#fff;border:none;border-radius:5px;cursor:pointer;transition:background-color .3s ease;transform:rotate(90deg)}.VisionContainer .VisionNav .nav-button:hover{text-decoration:underline}@keyframes fadeIn{0%{opacity:0;transform:translateY(20px)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeOut{0%{opacity:1;transform:translateY(0)}100%{opacity:0;transform:translateY(20px)}}